
* {
  -webkit-box-sizing: border-box;
}

body, html, body > div, #page {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  font: 17px/1.5 "Lucida Grande", "Lucida Sans Unicode", Helvetica, Arial, Verdana, sans-serif;
  background: white;
  margin: 0;
  color: #33333d;
  overflow-y: scroll;
  overflow-x: hidden;
}

a {
  color: #00a9e6;
}

section {
  min-height: 50%;
}

section:not(:last-child) {
  border-bottom: 1px solid #eee;
}

section:not(#top) {
  padding: 50px 25px;
  text-align: center;
}

pre {
  background: white;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  font-size: .9em;
  font-family: monospace;
  overflow-x: auto;
}

.content img {
  width: 100%;
}

p {
  font-weight: 300;
  font-family: "Lucida Grande", "Lucida Sans Unicode", Helvetica, Arial, Verdana, sans-serif;
}

code {
  font-family: monospace;
  font-size: .9em;
}

h1 {
  margin-top: 0;
  font-size: 2em;
}

h1:not(:first-child) {
  margin-top: 50px;
}

h2 {
  margin-top: 80px;
  font-size: 1.2em;
}

h3 {
  margin-top: 40px;
  font-size: 1em;
}

ul li {
  font-size: .8em;
}

section:nth-child(2n) {
  background: #fdfdfd;
  box-shadow: inset 0 1px 0 0 white;
}

.content {
  margin: 0 auto;
  max-width: 750px;
  text-align: left;
}

#top {
  height: 100%;
}

#heading {
  -webkit-user-select: none;
  position: absolute;
  top: 50%;
  margin-top: -150px;
  text-align: center;
  width: 100%;
}

#logo img{
  width: 50%;
  max-width: 600px;
  min-width: 375px;
}

#menu {
  position: fixed;
  top: 35px;
  right: 30px;
  z-index: 50;
}

#menu:hover ul {
  display: block;
}

#menu a {
  color: inherit;
}

#menu a#toggle {
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px;
  background: rgba(255,255,255,.9);
  border-radius: 2px;
  border: 1px solid transparent;
  z-index: 5;
  text-decoration: none;
  font-size: 25px;
}

#menu:hover a#toggle {
  border: 1px solid #efefef;
  border-bottom: none;
}

#menu ul {
  display: none;
  position: absolute;
  top: 45px;
  right: 0;
  margin: 0;
  border: 1px solid #efefef;
  border-bottom: 1px solid #ddd;
  border-radius: 2px;
  padding: 25px;
  background: rgba(255,255,255,.95);
  box-shadow: 0 1px 3px 0 #eee;
}

#menu ul li {
  list-style: none;
  font-size: 14px;
}

#menu ul li a {
  display: block;
  text-decoration: none;
  padding: 3px 0;
}

#menu ul li a:hover {
  text-decoration: underline;
}

#loading-screen {
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: #2b303b;
  opacity: 0;
  transition: opacity 0.25s ease-in-out;
}
#loading-screen.loading { opacity: 1; }

.loader,
.loader:before,
.loader:after {
  background: #ffffff;
  -webkit-animation: load 1s infinite ease-in-out;
  animation: load 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  animation-delay: -0.32s;
}
.loader {
  color: #ffffff;
  text-indent: -9999em;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  font-size: 11px;
  animation-delay: -0.16s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}


h1[align="center"] {
  display: none;
}

@media screen and (max-width: 700px) {
  #heading {
    margin-top: -75px;
  }

  #logo {
    font-size: 75px;
  }
}

@media screen and (max-width: 500px) {
  body {
    font-size: 13px;
  }
}



